<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<span> </span>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.customerMobile" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getProducts"></el-button>
				</el-form-item>
				<el-form-item style="float: right;">
					{{total}}
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="products" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column type="index" label="" width="80">
				<template slot-scope="scope">
					<span v-text="Number(scope.$index) + 1 + (PageNum - 1) * 10"></span>
				</template>
			</el-table-column>
			<el-table-column prop="bannerUrl" label="" width="130">
				<!--  -->
				<template slot-scope="scope">
					<img :src="scope.row.bannerUrl" height="40" width="80" style="margin-top: 10px;" />
				</template>
			</el-table-column>
			<el-table-column prop="name" label="" min-width="130">
			</el-table-column>
			</el-table-column>
			<el-table-column prop="customerMobile" label="" min-width="180">
			</el-table-column>
			<el-table-column prop="createTime" label="" min-width="130">
			</el-table-column>
			<el-table-column label="" width="90">
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
			<el-table-column label="" width="90">
				<template slot-scope="scope">
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-dialog title="" :visible.sync="editFormVisible">
			<el-form :model="editForm">
				<el-row class="ck_tab">
					<el-col :span="24">
						<el-form-item>
							<p><img :src="editForm.bannerUrl" width="414" height="200"  style="margin: 0 auto;"/></p> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.name}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12"> 
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.price}}</span>  
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.servicePrice}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.customerMobile}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.categorySec}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.firstNoticeNo}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.firstNoticeTime}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.applyRange}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.registerNoticeNo}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.registerNoticeTime}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.inlandFlag}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.areaCode}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.laterAppointTime}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.expireTime}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.priorityTime}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.internationRegisterTime}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.deadlineyear}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.publicFlag}}</span> 
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
							<span><span style="color: #3A3B8C;"></span>{{editForm.createTime}}</span>  
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
		</el-dialog>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
	</section>
</template>

<script>
	import {
		brandgetList,
		brandcreate,
		branddelete,
		brandgetById
	} from '../../api/api';
	export default {

		data() {
			return {
				filters: {
					name: '',
					bannerUrl: '',
					createTime: '',
					customerMobile: '',
				},
				type: [],
				products: [],
				total: 0,
				PageNum:'1',
				listLoading: false,
				sels: [], // 
				editFormVisible: false, // 
				//
				editForm: { 
				},

			}
		},
		methods: {
			//pageNum
			handleCurrentChange(val) {
				this.pageNum = val
				this.getProducts();
			},
			handleRemove(files) {
				console.log(files);
			},
			handlePreview(files) {
				console.log(files);
			},
			
			//
			getProducts() {
				let para = {
					pageNum: this.pageNum,
					pageSize: 10,
					inlandFlag: 'Y',
					customerMobile: this.filters.customerMobile,
				};
				this.listLoading = true;
				//NProgress.start();
				brandgetList(para).then((res) => {
					//console.log(res.data.result);
					this.total = res.data.result.total;
					this.products = res.data.result.result;  
					this.pageNum=res.data.result.pageNum; 
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					this.listLoading = false;
				});
			},
			//
			handleEdit: function(index, row) {
				this.editFormVisible = true;
				this.editForm = {
					name: row.name,
					price: row.price,
					servicePrice: row.servicePrice,
					categorySec: row.categorySec,
					firstNoticeNo: row.firstNoticeNo,
					bannerUrl: row.bannerUrl,
					firstNoticeTime: row.firstNoticeTime,
					applyRange: row.applyRange,
					registerNoticeNo: row.registerNoticeNo,
					registerNoticeTime: row.registerNoticeTime,
					inlandFlag: row.inlandFlag,
					areaCode: row.areaCode,
					laterAppointTime: row.laterAppointTime,
					expireTime: row.expireTime,
					priorityTime: row.priorityTime,
					internationRegisterTime: row.internationRegisterTime,
					deadlineyear: row.deadlineyear,
					publicFlag: row.publicFlag,
					createTime: row.createTime,
					auditDesc: row.auditDesc,
					customerMobile: row.customerMobile,
				} 
			},
			//
			handleDel: function(index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: row.id
					};
					branddelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			},

			selsChange: function(sels) {
				this.sels = sels;
			},
			//
			batchRemove: function() {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = {
						ids: ids
					};
					branddelete(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getProducts();
					});
				}).catch(() => {

				});
			}
		},
		computed: {

		},

		components: {

		},
		mounted() {
			//this.page = Number(sessionStorage.pageIndex);
			this.getProducts();
			this.configgetList()
		}
	}
</script>

<style scoped>
	.l-style {
		color: #0082E6;
		margin-right: 5px;
	}
	
	.ck_tab span {
	font-size: 16px;
	}
</style>


